<template>
    <div class="wrapper" ref="wrapper">
        <div class="city-info">
            <div class="my-location">
                <div class="my-location-tit border-topbottom">你的位置</div>
                <ul class="my-location-list">
                    <li class="my-location-item item-alone" v-text="defaultCity"></li>
                </ul>
            </div>
            <div class="my-location">
                <div class="my-location-tit border-topbottom">热门城市</div>
                <ul class="my-location-list">
                    <li class="my-location-item" v-for="(cityItem,index) of hotCities" :key="index" @click="setCity($event)" v-text="cityItem"></li>
                </ul>
            </div>
            <div class="my-location" v-for="item of citys" :key="item.title" :ref="item.title">
                <div class="my-location-tit border-topbottom" v-text="item.title"></div>
                <ul class="region-list" v-for="city of item.lists" :key="city.cityId">
                    <li class="region-item border-bottom" @click="setCity($event)" v-text="city.cityName"></li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapState } from 'vuex'
export default {
    name: 'CityListContent',
    props: {
        hotCities: Array,
        citys: Array,
        letter: [String,Number]
    },
    computed: {
        ...mapState({
            'defaultCity': 'city'
        })
    },
    methods: {
        setCity(e) {
            this.$store.commit('setDefaultCity',e.target.innerHTML);
            this.$router.push('/');
        }
    },
    mounted() {
        this.scroll = new BScroll(this.$refs.wrapper, { 
            click: true,   //派发点击事件
            mouseWheel: true  //派发鼠标滚轮事件
        })
    },
    watch: {
        letter() {
            if(this.letter){
                const ele = this.$refs[this.letter][0]; //获取到具体的DOM元素
                this.scroll.scrollToElement(ele); //滚动到目标元素
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~@/assets/stylus/mixin'
    .wrapper
        position absolute
        top 1.5rem
        right 0
        bottom 0
        left 0
        overflow hidden
        .city-info
            boxCenter()
            .my-location
                .border-bottom
                    &:after
                        border-color #ccc
                &-tit
                    color #333
                    font-weight bold
                    padding .15rem .2rem
                    font-size .3rem
                    text-align left 
                    background-color #F5F5F5
                &-list
                    color #555
                    padding .2rem .2rem 0 .2rem
                    background-color #fff
                    overflow hidden
                    .my-location-item
                        ellipsis()
                        float left
                        font-size .26rem
                        width 2rem
                        height .6rem
                        line-height .6rem
                        margin-right .2rem  
                        margin-bottom .2rem  
                        font-weight bold 
                        text-align center
                        border .02rem solid #ccc
                    .item-alone
                        border-color #4CD0E1
                .region-list
                    color #333
                    font-size .3rem
                    .region-item
                        font-weight 800
                        height .7rem
                        line-height .7rem
                        padding-left .2rem
                        text-align left
        .dom
            z-index 10
            position absolute
            top 0
            left 0 
            width 2rem
            height 2rem
</style>




