<template>
    <div class="gallery">   <!-- 图片画廊组件 -->
        <div class="gallery-wrap">
            <swiper :options="swiperOption" class="gallery-inner" v-if="flag">
                <swiper-slide v-for="gallery in filterGallerys" :key="gallery.galleryId">
                    <slot :gallery="gallery"></slot>   <!-- 作用域插槽 -->
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        </div>
    </div>    
</template>

<script>
import { mapState,mapMutations } from "vuex"
export default {
    name: 'Gallery',
    props: {
        filterGallerys: {
            type: Array,
            required: true
        },
        initialIndex: {
            type: [Number,String],
            required: false,
            default: 0
        }
    },
    computed: {
        flag() {
            return this.filterGallerys.length;
        },
        swiperOption() {
            let loop = this.flag > 1 ? true : false;  //判断是否可以循环滑动
            return {
                initialSlide: this.initialIndex,
                autoplay: false,
                loop: loop,
                pagination: '.swiper-pagination',
                paginationType: 'fraction'
            }
        }
    },
    mounted() {
        document.body.style.overflow = 'hidden';
    }
}
</script>

<style lang="stylus" scoped>
    @import '~@/assets/stylus/mixin'
    .gallery-wrap >>> .gallery-inner
        .swiper-pagination
            color #fff
            font-size .36rem
            bottom .2rem
    .gallery
        boxCenter()
        z-index 10
        position fixed
        top 0
        right 0
        bottom 0
        left 0
        background #000
        &-wrap
            width 100%
            height 100% 
            display flex
            align-items center
</style>




