<template>
  <div id="app">
    <keep-alive exclude="CityDetail">
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
html,body {
  width: 100%;
  height: 100%;
}
#app {
  max-width: 750px;
  width: 100%;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #555;
  margin: 0 auto;
  overflow: hidden;
}
</style>



