<template>
    <div class="home-icon">
        <div class="icon-list">
            <swiper class="swiper-alone" :options="swiperOption" v-if="flag">
                <swiper-slide v-for="(page,index) of pages" :key="index">  <!-- 分页 -->
                    <div class="icon-item" v-for="icon of page" :key="icon.resouceId"> <!-- 当前页数据 -->
                        <div class="icon-wrap">
                            <img class="icon-pic" :src="icon.resouceUrl" alt="">
                        </div>
                        <div class="icon-txt" v-text="icon.resouceTxt"></div>
                    </div>
                </swiper-slide>
                <div class="swiper-pagination"  slot="pagination"></div>  <!-- 分页器 -->
            </swiper>
        </div>
    </div>    
</template>

<script>
export default {
    name: 'HomeIcons',
    data() {
        return {
            swiperOption: {
                loop: true,
                autoplay: false,
                pagination: '.swiper-pagination',
                paginationClickable: true
            }
        }
    },
    props: {
        iconList: Array
    },
    computed: {
        flag() {
            return this.iconList.length;
        },
        pages() {
            const pages = [];
            this.iconList.forEach((item,index) => {  //一维数组拆分成二维数组
                const page = Math.floor(index / 8);
                if(!pages[page]){
                    pages[page] = [];
                }
                pages[page].push(item);
            });
            return pages;
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~@/assets/stylus/mixin'
    .home-icon
        boxCenter()
        & >>> .swiper-alone
            height 0
            padding-bottom 53%
            overflow visible
            .swiper-pagination
                display flex
                justify-content center
                margin-bottom -.3rem
                .swiper-pagination-bullet
                    pagination() 
        .icon-list
            overflow hidden
            height 0
            padding-bottom 53%
            margin .2rem .2rem 0 .2rem
            .icon-item
                float left
                overflow hidden
                width 25%
                height 0
                padding-bottom 25%
                position relative
                .icon-wrap
                    position absolute
                    top 0
                    left 0
                    right 0
                    bottom .54rem
                    .icon-pic
                        height 100%
                        margin 0 auto
                .icon-txt
                    ellipsis()
                    width 1.78rem
                    position absolute
                    left 0
                    right 0
                    bottom 0
                    height .5rem
                    line-height .5rem
                    font-size .3rem
                    text-align center
</style>





