<template>
    <div class="wrap-inner">
        <home-header></home-header>
        <home-swiper :swiperList="swiperList" ></home-swiper>
        <home-icons :iconList="iconList"></home-icons>
        <home-love :loveList="loveList"></home-love>
    </div>
</template>

<script>
import axios from 'axios'
import HomeHeader from './components/HomeHeader'
import HomeSwiper from './components/HomeSwiper'
import HomeIcons from './components/HomeIcons'
import HomeLove from './components/HomeLove'
export default {
    name: 'Home',
    data() {
        return {
            swiperList: [],
            iconList: [],
            loveList: []
        }
    },
    components: {
        HomeHeader,
        HomeSwiper,
        HomeIcons,
        HomeLove
    },
    methods: {
        init() {
            axios.get('/Project/tourism/static/mock/home_swiper.json').then(this.getSwiperData);
            axios.get('/Project/tourism/static/mock/home_icons.json').then(this.getIconsData);
            axios.get('/Project/tourism/static/mock/home_love.json').then(this.getLoveData);
        },
        getSwiperData(res) {
            res = res.data;
            if(res && res.ret){
                this.swiperList = res.sliders;
            }
        },
        getIconsData(res) {
            res = res.data;
            if(res && res.ret){
                this.iconList = res.icons;
            }
        },
        getLoveData(res) {
            res = res.data;
            if(res && res.ret){
                this.loveList = res.loves;
            }
        }
    },
    mounted() {
        this.init();
    }   
}
</script>

<style lang="stylus" scoped>
    .wrap-inner
        width 100%
        height 100%
        overflow-x hidden
        overflow-y scroll
        &::-webkit-scrollbar
            display none
</style>





