import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import CityList from '@/pages/city_list/CityList'
import CityDetail from '@/pages/city_detail/CityDetail'
import AdmissionTicket from '@/pages/city_detail/components/AdmissionTicket'
import OneDayTour from '@/pages/city_detail/components/OneDayTour'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/Project/tourism/',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/city_list',
      name: 'CityList',
      component: CityList
    },
    {
      path: '/city_detail/:id',
      name: 'CityDetail',
      component: CityDetail,
      redirect: AdmissionTicket,
      children: [
        {
          path: 'admission_ticket',
          name: 'AdmissionTicket',
          component: AdmissionTicket
        },
        {
          path: 'one_day_tour',
          name: 'OneDayTour',
          component: OneDayTour
        }
      ]
    }
  ]
})



