<template>
    <div class="home-head">
        <div class="iconfont icon-left">&#xe624;</div>
        <div class="head-search">
            <span class="iconfont icon-search">&#xe632;</span>
            <input class="head-input" type="text" placeholder="请输入城市" />
        </div>
        <div class="head-city">
            <router-link to="/city_list" class="city-link">
                <span class="city" v-text="defaultCity"></span>
                <span class="iconfont icon-right">&#xe6aa;</span>
            </router-link>
        </div>
    </div>    
</template>

<script>
import { mapState } from 'vuex'
export default {
    name: 'HomeHeader',
    computed: {
        ...mapState({
            'defaultCity': 'city'
        })
    }
}
</script>

<style lang="stylus" scoped>
    @import '~@/assets/stylus/mixin'
    .home-head
        boxCenter()
        z-index 10
        position fixed
        top 0
        box-sizing border-box
        background-color $LightBlue
        .icon-left
            color #fff
            float left
            width .8rem
            height 0.95rem
            line-height 0.95rem
            font-size .28rem
        .head-search 
            float left
            width 5.4rem
            height .65rem
            line-height .65rem
            margin .15rem 0
            position relative
            font-size .12rem
            background-color #fff
            .icon-search 
                position absolute
                top 0
                left 0
                width .7rem
                font-size .28rem
            .head-input 
                position absolute
                top 0
                left .7rem
                width 4.6rem
                height 100%
                border-style none 
                outline-style none
                font-size .28rem
        .head-city 
            float right
            width 1.3rem
            height .95rem
            line-height .95rem
            text-align center
            .city-link
                color #fff
                display inline-block
                .city
                    ellipsis()
                    min-width .56rem
                    max-width .85rem
                    float left
                    font-size .28rem
                .icon-right
                    float left
                    font-size .3rem

</style>





