<template>
    <div class="swiper-wrap">
        <swiper :options="swiperOption" v-if="flag" class="home-swiper" ref="mySwiper">
            <!-- slides -->
            <swiper-slide class="slider-sty" v-for="item of swiperList" :key="item.resouceId">
                <img class="slider-pic" :src="item.resouceUrl" alt="" />
            </swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination"  slot="pagination"></div>  <!-- 分页器 -->
            <div class="swiper-button-prev" slot="button-prev"></div> <!-- 上一页 -->
            <div class="swiper-button-next" slot="button-next"></div> <!-- 下一页 -->
            <div class="swiper-scrollbar"   slot="scrollbar"></div>   <!-- 滚动条 -->
        </swiper>    
    </div>
</template>

<script>
export default {
    name: 'HomeSwiper',
    data() {
        return {
            swiperOption: {
                direction: 'horizontal', //轮播方向----horizontal水平（默认） vertical垂直
                loop: true, //轮播项循环
                autoplay: 3000,  //自动轮播延迟时间
                autoplayDisableOnInteraction: false, //用户拖动swiper不停止轮播
                pagination: '.swiper-pagination', //显示分页器
                paginationClickable: true, //分页器可以点击切换
                paginationType: 'bullets', //分页器类型---‘bullets’ 圆点（默认）‘fraction’ 分式 ‘progress’ 进度条
                nextButton: '.swiper-button-next', //显示并可以操作下一页按钮
                prevButton: '.swiper-button-prev', //显示并可以操作上一页按钮
                scrollbar: '.swiper-scrollbar', //显示滚动条
                scrollbarHide: false,  //滚动条不自动隐藏
                scrollbarDraggable: true //允许拖动滚动条
            }
        }
    },
    props: {
        swiperList: Array
    },
    computed: {
        flag() {
            return this.swiperList.length;
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~@/assets/stylus/mixin'
    .swiper-wrap 
        margin-top .95rem
        & >>> .home-swiper
            width 100%
            .slider-sty 
                .slider-pic
                    width 7.5rem
                    height auto
                    margin-top 0
            .swiper-pagination
                .swiper-pagination-bullet
                    pagination()
            .swiper-button-prev
                background-size .5rem .5rem
            .swiper-button-next
                background-size .5rem .5rem
            .swiper-scrollbar 
                position static!important
                width 7.5rem!important
                margin -.16rem auto 0
</style>





