<template>
    <div class="home-love-wrap">
        <div class="home-love">
            <div class="home-love-tit">
                <span class="iconfont icon-love">&#xe65c;</span>猜您喜欢
            </div>
            <ul class="love-list">
                <router-link tag="li" :to="`/city_detail/${loveItem.loveId}`" class="love-list-item" v-for="loveItem in loveList" :key="loveItem.loveId">
                    <div class="item-wrap">
                        <span class="item-wrap-sign">随买随用</span>
                        <img class="item-wrap-pic" :src="loveItem.lovePicUrl" alt="">
                    </div>
                    <div class="item-cont">
                        <h3 class="item-cont-tit" v-text="loveItem.loveTit"></h3>
                        <p class="item-cont-comment">
                            <span class="iconfont icon-star">&#xe62e;</span>
                            <span v-text="loveItem.loveComment"></span>条评论
                        </p>
                        <div class="item-cont-addr">
                            <div class="addr-left">
                                <span class="addr-left-yen">&#165;</span>
                                <span class="addr-left-money" v-text="loveItem.loveMoney"></span>起
                            </div>
                            <div class="addr-right" v-text="loveItem.loveAddr"></div>
                        </div>
                    </div>
                </router-link>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name: 'HomeLove',
    props: {
        loveList: {
            type: Array
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~@/assets/stylus/mixin' 
    .home-love-wrap
        background-color #f5f5f5
        padding-top .2rem
        .home-love
            boxCenter()
            background-color: #fff
            &-tit
                color #333
                font-size .36rem
                text-align left
                padding .2rem .3rem
                .icon-love
                    display inline-block
                    color #D1525C
                    width .4rem
                    height .4rem
                    font-size .4rem
                    margin-right .05rem
            .love-list
                width 7.5rem
                font-size .2rem
                padding-left .3rem
                box-sizing border-box
                &-item
                    height 2.3rem 
                    display flex
                    justify-content flex-start
                    align-items center
                    padding-right .3rem
                    border-bottom 0.02rem solid #eee
                    .item-wrap
                        width 26.67%
                        height 0
                        padding-bottom 26.67%
                        overflow hidden
                        position relative
                        &-sign
                            color #fff
                            position absolute
                            top 0
                            left 0
                            font-size .24rem
                            padding .07rem
                            border-radius 0 0 .2rem 0
                            background-color $orange
                        &-pic
                            width 100%
                    .item-cont
                        width 70%
                        text-align left
                        margin-left .3rem
                        &-tit
                            ellipsis()
                            line-height .44rem
                            font-size .32rem 
                        &-comment
                            color #999
                            margin-top .2rem
                            margin-bottom .2rem
                            font-size .3rem
                            .icon-star
                                color $orange
                                font-size .3rem
                                margin-left -.1rem
                                margin-right .1rem
                        &-addr
                            .addr-left
                                float left
                                &-yen
                                    color $orange
                                    font-size .3rem
                                &-money
                                    color $orange
                                    font-size .5rem
                            .addr-right
                                float right
                                margin-top .2rem
                                font-size .3rem
</style>




