<template>
    <div class="city-detail">
        <router-link class="detail-return" to="/">
            <span class="iconfont detail-return-icon">&#xe624;</span>
        </router-link>
        <swiper :options="swiperOption" class="swiper-wrap" v-if="flag" ref="swiperBox">
            <swiper-slide class="swiper-box" v-for="gallery in galleryObj.filterGallerys" :key="gallery.galleryId">
                <img class="swiper-pic" :src="gallery.galleryPicUrl" alt="">
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
        <gallery v-if="galleryFlag" v-bind="galleryObj" @click.native="setGalleryFlag">     <!-- 组件添加原生事件使用.native修饰符 -->
            <template slot-scope="{ gallery }">                                             <!-- 作用域插槽 -->
                <img class="gallery-pic" :src="gallery.galleryPicUrl" alt="" @click.stop>   <!-- 作用域插槽 -->
            </template>                                                                     <!-- 作用域插槽 -->
        </gallery>
        <city-detail-content :gid="gid"></city-detail-content>
    </div>    
</template>

<script>
import axios from 'axios'
import { mapState,mapMutations } from 'vuex'
import Gallery from '@/pages/components/Gallery'
import CityDetailContent from './components/CityDetailContent'
export default {
    name: 'CityDetail',
    data() {
        return {
            galleryObj: {
                filterGallerys: [],
                initialIndex: 0
            },
            galleryFlag: false,
            gid: -1
        }
    },
    components: {
        Gallery,
        CityDetailContent
    },
    computed: {
        flag() {
            return this.galleryObj.filterGallerys.length;
        },
        swiperOption() {
            let loop = this.flag > 1 ? true : false;  //判断是否可以循环滑动
            return {
                autoplay: false,
                loop: loop,
                pagination: '.swiper-pagination',
                paginationType: 'fraction',
                onClick: () => {    //通过箭头函数把this指向vue实例
                    this.galleryFlag = true;
                    let swiper = this.$refs.swiperBox.swiper;
                    let i = swiper.realIndex;  //拿到活动项的索引，复制块的数量不会计算在内
                    this.galleryObj.initialIndex = i;
                }
            }
        }
    },
    methods: {
        init() {
            axios.get('/Project/tourism/static/mock/city_detail.json').then(this.getGalleryData);
        },
        getGalleryData(res) {
            res = res.data;
            if(res && res.ret) {
                const _this = this;
                const id = this.$route.params.id;
                res.gallerys.forEach(function(item) {
                    if(id == item.gid) {
                        _this.gid = id;
                        _this.galleryObj.filterGallerys = item.galleryData;
                    }
                });
            }
        },
        setGalleryFlag() {
            this.galleryFlag = false;
            document.body.style.overflow = 'visible';
        }
    },
    mounted() {
        this.init();
    }
}
</script>


<style lang="stylus" scoped>
    @import '~@/assets/stylus/mixin' 
    .city-detail 
        boxCenter()
        position relative
        .detail-return
            z-index 2 
            display flex
            justify-content center
            align-items center
            position absolute
            top .2rem
            left .2rem
            width .7rem
            height .7rem
            border-radius 50%
            background-color rgba(0,0,0,0.5)
            &-icon
                color #fff
                font-size .32rem
        & >>> .swiper-wrap
            .swiper-box
                overflow hidden
                height 0
                padding-bottom 55%
                .swiper-pic
                    width 100%
            .swiper-pagination
                bottom .1rem
                color #fff 
                font-size .3rem
                text-align right
                padding-right .3rem
                box-sizing border-box
        & >>> .gallery-pic
            width 100%
</style>




