<template>
    <div class="city-choice-head">
        <div class="city-choice-wrap">
            <router-link to="/" class="icon-link">
                <span class="iconfont direction-left">&#xe624;</span>
            </router-link>
            <div class="city-choice">城市选择</div>
        </div>
        <div class="city-search">
            <input class="search-btn" v-model="keyWord" type="text" placeholder="请输入城市名/拼音" />
        </div>
        <div class="city-result" v-show="resultFlag">
            <ul class="result-list" v-show="contentFlag">
                <li class="result-list-item border-bottom" v-for="search in searchData" :key="search.cityId" @click="setCity($event)" v-text="search.cityName"></li>
            </ul>
            <div class="result-cont" v-show="!contentFlag">
                <span class="iconfont result-cont-icon">&#xe69d;</span>没有找到相应的城市
            </div>
        </div>
    </div>    
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    name: 'CityListSearch' ,
    props: {
        citys: {
            type: Array
        }
    },
    data() {
        return {
            keyWord: '',
            resultFlag: false,
            searchData: []
        }
    },
    computed: {
        contentFlag() {
            return this.searchData.length
        }
    },
    watch: {
        keyWord() {
            this.searchData = [];
            const _length = this.keyWord.length;
            if(_length > 0) {
                this.resultFlag = true;
                const that = this;
                this.citys.forEach( item => {
                    item.lists.forEach( listItem => {
                        let _key = that.keyWord.toLowerCase(); 
                        let _index1 = listItem.cityName.indexOf(that.keyWord);  //城市名模糊匹配
                        let _index2 = listItem.citySpell.substr(0,_length).indexOf(_key);  //城市全拼模糊匹配
                        // _key == listItem.simplicity  城市简拼精准匹配
                        if(_index1 > -1 || _index2 > -1 || _key == listItem.simplicity) {
                            that.searchData.push(listItem);
                        }
                    });
                });
            }else {
                this.resultFlag = false;
            }
        }
    },
    methods: {
        ...mapMutations([
            'setDefaultCity'
        ]),
        setCity(e) {
            this.setDefaultCity(e.target.innerText);
            this.$router.push('/');
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~@/assets/stylus/mixin'
    .city-choice-head
        boxCenter()
        z-index 12
        color #fff
        position fixed
        background-color #4CD0E1
        .city-choice-wrap
            position relative
            height .8rem
            .icon-link
                color #fff
                position absolute
                top 0
                left .2rem
                height .8rem
                .direction-left
                    display block
                    line-height .8rem
                    font-size .3rem
            .city-choice
                font-size .36rem
                line-height .8rem
                text-align center
        .city-search
            height .7rem
            overflow hidden
            .search-btn
                color gray
                display block
                width 7rem
                height .6rem
                margin 0 auto
                padding 0 .2rem 
                font-size .28rem
                text-align center
                border-style none 
                outline-style none
                background-color #fff
                box-sizing border-box
        .city-result
            z-index 12
            position fixed
            top 1.5rem
            max-width 750px
            width 100%
            height 100%
            overflow hidden
            background-color #fff
            .result-list
                height 100%
                overflow auto
                &::-webkit-scrollbar 
                    display none
                &-item
                    color #555
                    height .6rem
                    line-height .6rem
                    font-size .3rem
                    text-align left
                    padding-left .3rem
            .result-cont
                color #555
                position absolute
                top -3rem
                right 0
                bottom 0
                left 0
                height .4rem
                line-height .4rem
                font-size .3rem
                margin auto
                &-icon
                    display inline-block
                    color #4CD0E1
                    font-size .34rem
                    margin-right .1rem
</style>




