<template>
    <div class="city-detail-wrap">
        <div class="detail-head border-bottom">
            <div class="detail-head-nav">
                <router-link class="detail-link" :to="path1">门票</router-link>    
            </div>
            <div class="detail-head-nav">
                <router-link class="detail-link" :to="path2">一日游</router-link>    
            </div>  
        </div>
        <div class="detail-cont">
            <transition appear name="fade" mode="out-in">
                <router-view></router-view>
            </transition>
        </div>
    </div>
</template>

<script>
export default {
    name: 'CityDetailContent',
    props: {
        'gid': [Number,String]
    },
    computed: {
        path1() {
            return `/city_detail/${this.gid}/admission_ticket`;
        },
        path2() {
            return `/city_detail/${this.gid}/one_day_tour`;
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~@/assets/stylus/mixin'
    .city-detail-wrap >>> .detail-link
        display inline-block
        width 100%
    .city-detail-wrap >>> .router-link-active
        color #fff
        background-color $LightBlue
    .city-detail-wrap
        .detail-head
            height .8rem
            &-nav
                float left
                width 50%
                line-height .8rem
                font-size .32rem
        .detail-cont
            .fade-enter,.fade-leave-to
                opacity 0
                transform translate(-7.5rem)
            .fade-enter-active,.fade-leave-active
                transition all .2s
</style>





