<template>
    <div>
       <city-list-search :citys="citys"></city-list-search> 
       <city-list-content :hotCities="hotCities" :citys="citys" :letter="letter"></city-list-content> 
       <alphabet :citys="citys" @ele-click="scrollToEle"></alphabet>
    </div>    
</template>

<script>
import CityListSearch from './components/CityListSearch'
import CityListContent from './components/CityListContent'
import Alphabet from './components/Alphabet'
import axios from 'axios'
export default {
    name: 'CityList',
    data() {
        return {
            hotCities: [],
            citys: [],
            letter: ''
        }
    },
    components: {
        CityListSearch,
        CityListContent,
        Alphabet
    },
    methods: {
        init() {
            axios.get('/Project/tourism/static/mock/city_list.json').then(this.getHotCities)
        },
        getHotCities(res) {
            res = res.data;
            this.hotCities = res.hotCities;
            this.citys = res.citys;
        },
        scrollToEle(txtKey) {
            this.letter = txtKey;
        }
    },
    mounted() {
        this.init()
    }
}
</script>

<style lang="stylus" scoped>
    
</style>





