import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        city: localStorage.getItem('cityTxt') || '三亚'
    },
    mutations: {
        setDefaultCity(state, defaultCity) {    //修改默认城市
            localStorage.setItem('cityTxt',defaultCity)
            state.city = defaultCity;
        }
    }
})


